
@media only screen and (min-width: 1836px) {
  /* For desktop: */
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Droid Sans", Helvetica, Arial, sans-serif;
  background: linear-gradient(-90deg,#ffffff , #ee7752, #e73c7e, #23a6d5,#23d5ab, #ca6af7, #000000);
  background-size: 400% 400%;
	animation: gradient 30s ease infinite;
	/* height: 100vh; */
}

@keyframes gradient {
  
	0% {
		background-position: 0% 50%;
    
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


.resume{
  background-color:#ffffff;
  margin: 2% 15%;
  padding: 20px 40px;
  color:black;
  height: 1300px;
  position: relative;
  box-shadow: 2px 1px 2px rgb(15, 15, 53);
  transition: 2s;
  border-radius: 15px;
}

.resume:hover{
  background-color:#ffffff;
  color:black;
  box-shadow: 40px -20px 30px #000000;
  transition: 2s;
  transform-style: preserve-3d;

}

.resume header{
  color: #000000;
  margin: -16px -36px;
  height: 10%;
  border-bottom: #000000 solid;
  background-color: #ffffff;
  display: flex;
  flex-direction: rows;
}

.resume footer{
  color: #000000;
  bottom: 0;
  margin: 5px -35px;
  position: absolute;
  width:99%;
  border-top: #000000 solid;
}


.qrbox{
  width: 120px;
  height: 110px;
  padding: 0 3px 0 0;
}
.qrbox img{
  width: 120px;
  height: 108px;
}

.headerbox{
  margin: -10px 0;

}

.headerbox p{
  margin: -22px 0px 0px -2px;
  font-size: medfium;
  padding: 5px;
}

.headerbox img{
  background-color: black ;
  margin: 7px 5px 0px 5px;
}

.content{
  display: flex;
}

.sidebar{
  background-color: #ffffff;
  margin-left: -35px;
  margin-top: 20px;
  color: #000000;
  width: 40%;
  height: 740px;
  padding: 10px;
}

.sidebar h4{
  font-weight: normal;
  background-color: #f7f7f7;
  color: #000000;
  border: #000000 solid;
  margin: 5px 0px 0px 0px;
  font-size: large;
}

.sidebar p{
  font-size: small;
  padding: 5px;
  margin: 0px;
}

.sidebar img{
  padding: 1px;
  margin: 7px 5px 0px 5px;
  width: 80px;
  height:25px;
}


.container{
  background-color: #ffffff;
  margin-left: 5px;
  margin-top: 25px;
  margin-right: -35px;
  padding: 10px;
  color: #000000;
  width: 25%;
  height: 1100px;
  border-left: #000000 solid;
  width: 100%;
}

.card{
  background-color: rgb(0, 0, 0);
}


.cardcontent{
  background-color: #ffffff;
  transition: 2s;
}
.cardcontent:hover{
  box-shadow: 10px -1px 30px #000000;
  transition: 0.001s;
}
}

